import Vue from "vue";
import Router from "vue-router";
import BreezeHeader from "./layout/AppHeader";
import AppFooter from "./layout/AppFooter";
import Components from "./views/Components.vue";

Vue.use(Router);

export default new Router({
  linkExactActiveClass: "active",
  routes: [
      {
          path: "/",
          name: "Breeze",
          components: {
              header: BreezeHeader,
              default: Components,
              footer: AppFooter
          }
      },
      {
          path: "/about",
          name: "About",
          components: {
              header: BreezeHeader,
              default: Components,
              footer: AppFooter
          }
      },
      {
          path: "/news",
          name: "News",
          components: {
              header: BreezeHeader,
              default: Components,
              footer: AppFooter
          }
      }
    ],
  scrollBehavior: to => {
    if (to.hash) {
      return { selector: to.hash };
    } else {
      return { x: 0, y: 0 };
    }
  }
});

